.contact_maincontent{
    max-width: 1920px;
   
}


p{
    color: #4AD592;
}
.contact_maincontainer{
 
    margin-bottom: 80px;
    
}

.h1_span{
    font-size: 90px;
    font-weight: bold;
    font-family: 'Baloo 2';
    color:#4AD592 ;
 margin-left: 152px;
}

.got_a_prob_p{
    font-weight: bold;
    font-family: 'Baloo 2';
    color:#4AD592 ;
    font-size: 32px;
    
}

.need_developer_text{
    color:#4AD592 ;
    font-family: 'Montserrat';
    font-weight: bold;
}

.contact_ball{
    height: 200px;
    width: 200px;
    margin-left: 00px;
    margin-top: 40px;
}

.arrow_up{
    height: 60px;

    margin-top: 200px;

transition: all 25ms ease-in-out;
    &:hover {
        transform: scale(1.1 ,1.1);
        -webkit-filter: invert(1);
        filter: invert(1);
      
      
    }
}

.responsiv_arrow_ball{
    display: none;
}

.contact_maincontainer{
    display: flex;
    justify-content: space-around;
    margin-right:156px;

}
.contact_left{
    margin-left: 24px;
    width: 500px;
}

@media (max-width:1669px) {
    .h1_span{
        margin-left: 90px;
    }
    
}

@media(max-width:1275px){
    .contact_maincontainer{
        flex-direction: column;

        align-items: center;
        margin-right: 0px;
    }
    .contact_left img {
        display: none;
    }
    .responsiv_arrow_ball{
        display: flex;
    }
 .h1_span{
align-self: center;
margin-left: 380px;

 }
 .got_a_prob_p{
    line-height: 0.8px;
margin-right: 60px;
 }

}

@media (max-width:1000px) {
    .h1_span{
        align-self: center;
        margin-left: 200px;
        
         }
}

@media (max-width: 745px) {
    .h1_span{
        font-size: 60px;
        margin-left: 150px;
    }
    .contact_maincontainer{
        p{
            font-size: 15px;
            margin-left: 40px;
        }
        span{
            font-size: 15px;
            margin-left: 40px;
            text-align: center;
        }
    }
}
@media (max-width: 595px) {
.h1_span{
    margin-left: 90px;
}

.contact_maincontainer{
    
    p{
        font-size: 10px;
        margin-left:1px;
        
        text-align: center;
    }
    span{
        font-size: 10px;
        margin-left: 27px;
        text-align: center;
    }
    margin-bottom: 0px;
}
.got_a_prob_p{
margin-left: 46px !important;
}

.arrow_up{
    margin-top: 80px;
}


}
@media (max-width:391px) {
.h1_span{
    margin-left: 59px;
}
.contact_left{
    width: 316px;
    margin-right: 24px;
    text-align: center;
}
    
}